.skills
{
    width: 100vw;
    height: 100vh;
    display: flex;
    padding: 6em;
    flex-direction: column;
    align-items: center;
    background-color: #fffffe;
    overflow-x: hidden;

}
.skills h1
{
    text-transform: uppercase;
    font-size: 2.5em;
    letter-spacing: .15em;
    color: #0f0e17;
    font-weight: bold;
}

.skills p
{
    font-size: 1em;
    padding: .5em;
    letter-spacing: 3px;
    color: #2e2f3e;
}

.skill-container 
{
    display: flex;
    flex-direction: column;
    width: 80%;
    z-index: 10;
    
}

.skill-row
{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px;
}
.skill-row .skill-row-child
{
    padding: 20px;
    background-color: #0f0e17;
    margin: 10px 20px;
    flex: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.skill-row .skill-row-child h2 
{
    color: #fffffe;
    padding: 10px;
}
.skill-row .skill-row-child h2 i
{
    margin-right: 10px;
}
.skill-row .skill-row-child div p {
    color: #a7a9be;
    padding: 10px;
    font-weight: bold;
}

@media (min-width: 320px) {
    .skill-row .skill-row-child
    {
        width: 200px;
        padding: 10px;
    }
    .skill-row .skill-row-child h2
    {
        font-size: 14px;
        font-weight: bold;
        padding: 0;
    }
    .skills
    {
        padding: 30px 0;
    }
    .skills h1
    {
        margin-top: 40px;
        font-size: 32px;
    }
    .skills-p
    {
        margin-top: 5px;
        font-size: 12px;
        text-align: center;
    }
    .skill-row .skill-row-child div p
    {
        font-size: 12px;
        padding: 0;
        margin: 5px 0;
    }
    .skill-row
    {
        padding: 0;
    }
}